import React from 'react'
import BannerVid from '../assets/gpt-banner-video.mp4'
import About from './About/About'
import Awards from './Awards/Awards'
import FeatureSlider from './FeatureSlider/FeatureSlider'
import './Home.css'
import ProfileCard from './ProfileCard/ProfileCard'
import Value from './ValuesSection/Value'

function Home() {
  return (
    <div className='Home'>
      <div className='overlay'></div>
        <video autoPlay muted loop src={BannerVid}/>
        

        <main>
        <section >#</section> 
               <section className='content'> 
                  <span>WeAreGirlPowerTalk</span>
                </section>
</main>
<FeatureSlider/>
<About/>
<Awards/>
<Value/>
<ProfileCard/>

    </div>
  )
}

export default Home